import { Streamlit, RenderData } from "streamlit-component-lib"

function onRender(event: Event): void {
  const data = (event as CustomEvent<RenderData>).detail

  // Remove existing content
  let child = document.body.lastElementChild;
  if (child) {
    document.body.removeChild(child)
  }

  // Add and style the image container
  let div = document.body.appendChild(document.createElement("div"))
  for (let key in data.args["div_style"]) {
    div.style[key as any] = data.args["div_style"][key]
  }

  // Add and style all images
  let imagesLoaded = 0
  for (let i = 0; i < data.args["paths"].length; i++) {
    let imgDiv = div.appendChild(document.createElement("div")) // Create a new div for each image and its title
    imgDiv.style.border = "1px solid black"; // Add a border to the div
    imgDiv.style.padding = "10px"; // Add some padding to the div
    imgDiv.style.cursor = "pointer";
    imgDiv.onclick = function (): void { // Move the onclick event to the div
      Streamlit.setComponentValue(i)
    }
    imgDiv.onmouseover = function (): void { // Add a mouseover event to darken the image
      img.style.opacity = "0.7";
    }
    imgDiv.onmouseout = function (): void { // Add a mouseout event to lighten the image
      img.style.opacity = "1.0";
    }
    let img = imgDiv.appendChild(document.createElement("img"))
    for (let key in data.args["img_style"]) {
      img.style[key as any] = data.args["img_style"][key]
    }
    img.src = data.args["paths"][i]
    if (data.args["titles"].length > i) {
      let imgTitle = imgDiv.appendChild(document.createElement("p")) // Create a new p element for the title
      imgTitle.innerText = data.args["titles"][i] // Set the innerText of the p element to the title
      imgTitle.style.paddingLeft = "8px"
    }
    // img.onclick = function (): void {
    //   Streamlit.setComponentValue(i)
    // }
    // eslint-disable-next-line
    img.onload = function (): void {
      imagesLoaded++
      if (imagesLoaded === data.args["paths"].length) {
        Streamlit.setFrameHeight()
      }
    }
  }
}

